@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
:root {
  --font-color: #d6d6d6;
  --font-color-dark:#16191b;
  
  --font-link: #6f6f6f;
  --font-link-dark: #b9b9b9;

  --font-link-hover: #a2a2a2;
  --font-link-hover-dark:#16191b;

  --background-color: #16191b;
  --background-color-light: #e7e7e7;
}
.dark {
  --bg: var(--background-color);
  --font: var(--font-color);
  --link: var(--font-link);
  --link-hover: var(--font-link-hover);
}
.light {
  --bg: var(--background-color-light);
  --font: var(--font-color-dark);
  --link: var(--font-link-dark);
  --link-hover: var(--font-link-hover-dark);
  /* --font-link: var(); */
}
html,body {
  height: 100%;
  margin:0;
}
body {
  text-align:left;
  font-family: 'Roboto Mono', monospace;
  letter-spacing: 0.25px;
}
#app {
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--font);
  min-width: 100%;
  min-height: 100%;
  background: var(--bg);
}
.crypto{
  color: #444444;
}
.crypto-post {
  color: var(--font);
}
.theme-switch {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    float: left;
    cursor: pointer;
}
.flex-child {
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
}
.theme-switch i {
    font-size: 20px;
}
.theme-switch .fa-moon {
  color:#777;
}
.hidden {
  display:none;
}
@media only screen and (max-width: 600px) {
#content {
      padding-top:5rem;
      margin-left:1.5rem;
      margin-right:1.5rem;
}
#top-bar {
      background: var(--bg);
      position: fixed;
      left:0;
      right:0;
      width:100%;
}
}


h1 {
    font-size: 1.3em;
}
h2 {
    font-size: 1.2em;
}
#content {
    margin-left:15rem;
    margin-right:15rem;
}
@media only screen and (max-width: 600px) {
#content {
        padding-top:5rem;
        margin-left:1.5rem;
        margin-right:1.5rem;
}
#header {
        background: var(--bg);
        position: fixed;
        left:0;
        right:0;
        width:100%;
        padding: 0.5rem;
}
#header p a {
        padding: 0 10px;
}
}
@media only screen and (min-width:600px) and (max-width: 1200px) {
#content {
        padding-top:5rem;
        margin-left:7.5rem;
        margin-right:7.5rem;
}
#header {
        background: var(--bg);
        position: fixed;
        left:0;
        right:0;
        width:100%;
        padding: 0.5rem;
}
#header p a {
        padding: 0 10px;
}
}
#header {
    text-align: right;
    padding:1rem;
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
}
#header p {
    display: inline;
}
#header p a{
    text-decoration: none;
    padding: 0 25px;
}
#header p a:link, #header p a:visited, #header p a:active, #haeder *{
    color:var(--font-link);
}
#header a:hover, .base_link:hover {
    text-decoration:underline;
    color:var(--link-hover)
}
#gds *, #gds *:hover {
    color: #2a9fe2;
}
#gds:link, #linkedin:link, #twitter:link, #github:link {
    text-decoration: none;
}
#gds *:hover, #linkedin:hover, #twitter:hover, #github:hover {
    background-color: transparent;
    text-decoration: underline;
}
#linkedin *, #linkedin:hover, #linkedin:visited{
    color: #0077B5; /* linkedIn blue */
}
#github *, #github:hover, #github:visited{
    color:#6a737d; /* github blue?*/
}
#twitter *, #twitter:hover, #twitter:visited{
    color: #1DA1F2; /* Twitter blue */
}
.base_link, .post_link{
    text-decoration: none;
}
.base_link *, .base_link:hover, .base_link:visited, .post_link {
    color:var(--font-link);
}
.post {
    margin-left: 30px;
    padding-left: 10px;
    border-left: 2px solid var(--font-link);
    padding-bottom: 5px;
    position: relative;
}
.post:hover {
    /* border-bottom: 2px solid var(--font-link); */
}
.post:after {
    content: '';
    position: absolute;
    width: 0;
    height: 1px;
    display: block;
    margin-top: 5px;
    right:0;
    background: #fff;
    -webkit-transition: width 1s ease;
    transition: width 1s ease;
}
.post:hover:after {
    width: 100%;
    left:0;
}
